.row-header{
    margin:0px auto;
    padding:0px auto;
}

.row-content {
    margin:0px auto;
    padding: 50px 0px 50px 0px;
    border-bottom: 1px ridge;
    min-height:400px;
}

.footer{
    background-color:rgb(165, 57, 253);
    margin:0px auto;
    padding: 20px 0px 20px 0px;
}
.mail{
    color: black;
}
.mail:hover{
    color: whitesmoke;
}
.link {
    color: black;
}
.link:hover {
    color: whitesmoke;
}
.jumbotron {
    background-image: url("https://images.pexels.com/photos/1565982/pexels-photo-1565982.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 550px;
    padding:250px 30px 70px 30px;
    margin:auto;
    color:floralwhite;
}

address{
    font-size:80%;
    margin:0px;
    color:#0f0f0f;
}

.navbar-light {
    background-color:whitesmoke;
}

.page-enter {
    opacity: 0.03;
    transform: translateX(-100%);
}
.page-enter-active {
    opacity: 1;
    transform: translateX(0%);
    transition: all 300ms ease-in;
}

.page-exit {
    opacity: 1;
    transform: translateX(0%);
}

.page-exit-active{
    opacity: 0.03;
    transform: translateX(100%);
    transition: all 300ms ease-out;
}